.card {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  align-items: center;
  min-height: 48px;
  padding: 16px 24px;
  justify-content: space-between;

  &:nth-child(odd) {
    background: #ECECF9;
  }

  .button {
    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-end;

    button {
      padding: 0 5px;

    }
  }
}

.cardItem {
  flex: 0 1 20%;
  display: flex;


  div {
    padding: 0 5px;
  }
span{
  button {
    color:#7f76b9;
    height: 0;
  }

}

}